import React, { useState,useEffect } from 'react';
import { useSelector} from 'react-redux'
import Paper from '@material-ui/core/Paper';
import { EditingState } from '@devexpress/dx-react-grid';
import axios from '../../axios'
import {
  Grid,
  Table,
  TableHeaderRow,
  TableEditRow,
  TableEditColumn,
} from '@devexpress/dx-react-grid-material-ui';
import { connect } from 'react-redux'
const mapStateToProps = state => ({
  Dept_ID: state.Dept_ID
});

const getRowId = row => row.id;

function EditInfrastructure() {
  const Dept_ID = useSelector(state => state.Dept_ID)
  const collegeID = useSelector(state=>state.collegeID)
const [data, setdata]=useState([])
  const [columns] = useState([
    { name: 'id', title: 'ID' },
    { name: 'Equipment_Name', title: 'Equipment Name' },
    { name: 'Quantity', title: 'Quantity' },
    { name: 'Remarks', title: 'Remarks' },
  ]);
  const [rows, setRows] = useState([]);
  const [tableColumnExtensions] = useState([
    { columnName: 'id', width: 40 },
  ]);
  const [editingRowIds, setEditingRowIds] = useState([]);
  const [addedRows, setAddedRows] = useState([]);
  const [rowChanges, setRowChanges] = useState({});

  const changeAddedRows = (value) => {
    const initialized = value.map(row => (Object.keys(row).length ? row : { Description: '' }));
    setAddedRows(initialized);
  };
  const [editingStateColumnExtensions] = useState([
    { columnName: 'id', editingEnabled: false },
    
  ]);
  const commitChanges = ({ added, changed, deleted }) => {
    let changedRows;
    let rowIDList;
    let rowID
    if (added) {
      const startingAddedId = rows.length > 0 ? rows[rows.length - 1].id + 1 : 0;
      changedRows = [
        ...rows,
        ...added.map((row, index) => ({
          id: startingAddedId + index,
          ...row,
        })),
      ];
    }
    if (changed) {
      changedRows = rows.map(row => (changed[row.id] ? { ...row, ...changed[row.id] } : row));
      rowIDList = rows.map(row => (changed[row.id] ? row.id : ''));
        for (var i = 0; i < rowIDList.length; i++) {
          if (rowIDList[i] >0) 
          {
            rowID=i;
          };
        }
      axios.post('api/infra/update/'+ rowIDList[rowID], changed[rowIDList[rowID]])
      .then(res => {
        if(res.data.flag==='success')
        {
         alert('The  Updated Successfully')
        }
      })
      .catch(error => {
        alert(error);
      });
    }
    if (deleted) {
      const deletedSet = new Set(deleted);
      changedRows = rows.filter(row => !deletedSet.has(row.id));
      const url = `api/infrastructure/Delete/`+ deleted[0];
      axios
        .delete(url)
        .then(res => {
            if(res.data.flag==='success')
            {
                alert('The Record deleted Successfully')
            }
        })
        .catch(err => {
             alert(err);
        });
    }
    setRows(changedRows);
  };
  useEffect(() => {
    axios.get(`api/Deptinfra/`+ Dept_ID+'/'+collegeID, {})
    .then(res => {
        setRows(res.data)
    })
    .catch((error) => {
        console.log(error)
    })
  }, []);
  return (
    <Paper>
      <Grid
        rows={rows}
        columns={columns}
        getRowId={getRowId}
      >
        <EditingState
          editingRowIds={editingRowIds}
          onEditingRowIdsChange={setEditingRowIds}
          rowChanges={rowChanges}
          onRowChangesChange={setRowChanges}
          addedRows={addedRows}
          onAddedRowsChange={changeAddedRows}
          onCommitChanges={commitChanges}
          columnExtensions={editingStateColumnExtensions}
        />
        <Table
          columnExtensions={tableColumnExtensions}
        />
        <TableHeaderRow />
        <TableEditRow />
        <TableEditColumn
          //showAddCommand={!addedRows.length}
          showEditCommand
          showDeleteCommand
        />
      </Grid>
    </Paper>
  );
};
export default connect(
    mapStateToProps,
  )(EditInfrastructure);